import { Component } from "react";
import { Link } from "react-router-dom";
import '../styles/search.css'
import Header from "./HeaderComponent";
class Search extends Component{
    render(){
        const data = this.props.data
        const postlink = `/user/`
        return(
            <div className="search-body" >
                <div className="profile-posts" >
                {data.map((item) => {
                    //console.log(item)
                    return <a>
                    {item.type === "video" ? 
                    <div className="profile-post-item"  ><iframe src={item.url}></iframe></div>:
                    <Link to={postlink+item.user+'/'+item.id}><div className="profile-post-item" style={{backgroundImage: `url(${item.url})`}}></div></Link>
                    }
                </a>
                })}
              </div>  
            </div>
        )
    }
}
export default Search